import React, { Component } from 'react';
import Modal from 'react-awesome-modal';

export default class PopupForm_Left extends Component {

    constructor(props) {
        super(props);
        this.state = {
            visible : false
        }
    }

    openModal() {
        this.setState({
            visible : true
        });
    }

    closeModal() {
        this.setState({
            visible : false
        });
    }

    render() {
        return (
            <section>
              <style jsx>{`
              .addTodo {
                width : 200px;
                color : red;
                font-size : 20px;
                padding : 5px;
                margin-top : 20px;
                margin-left : 10px;
                margin-right : 10px;
                display : flex;
                flex-direction : column;
                align-items : center;
                opacity : 0.7;

              }

              .addTodo:hover {
                opacity : 1;
              }

              .close {
                text-decoration : none;
                color : red;
              }
              `}</style>
                <input className="addTodo" type="button" value="계획 추가하기" onClick={() => this.openModal()} />
                <Modal
                    visible={this.state.visible}
                    width="400"
                    height="300"
                    effect="fadeInUp"
                    onClickAway={() => this.closeModal()}
                >
                    <div>
                        <h1>계획 추가하기</h1>
                        <div>
                        <form action="/create_process" method="post" class="addStudentForm" id="form">

    &nbsp;남학생<input type="radio" name="gender" value="male" checked required />
    &nbsp;여학생<input type="radio" name="gender" value="female" required />


    &nbsp;과목명&nbsp;<input type="text" name="subject" placeholder="ex) 물리" required></input>


    &nbsp;수업료(단위 : 만)&nbsp;<input class="inputTuition" type="number" name="tuition" placeholder="숫자만 입력" required />

    &nbsp;기타세부사항&nbsp;
    &nbsp;&nbsp;&nbsp;
    <textarea class="textarea" name="others"></textarea>


    <input class="complete-button" type="submit" value="완료" />


  <a href="/" class="closeForm">창닫기</a>
  </form></div>


                        <a className="close" href="javascript:void(0);" onClick={() => this.closeModal()}>닫기</a>
                    </div>
                </Modal>
            </section>
        );
    }
}
